.example {
  width: 320px;
  margin: 0 auto;

}

.example__wrapper {
  box-sizing: border-box;
  padding: 15px;
  background-color: $main-color;
}

@media (min-width: $tablet-width) {
  .example {
  width: 100%;
  }

  .example__wrapper {
    width: 768px;
    margin: 0 auto;
    box-sizing: border-box;
  }

}
