.page-header {
  width: 320px;
  margin: 0 auto;
}

.page-header__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 5px;
  padding: 15px;
  display: flex;
  box-sizing: border-box;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.main-nav__list {
  display: none;
}

@media (min-width: $tablet-width) {
  .page-header {
    width: 100%;
    min-height: 85px;
    margin: 0 auto;
  }

  .page-header__wrapper {
    width: 768px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .main-nav {
    min-width: 400px;
    display: flex;
    margin-left: auto;
    justify-content: space-around;
  }

  .main-nav__toggle {
    display: none;
  }

}
