.slogan {
  width: 320px;
  margin: 0 auto;
  }

.slogan__wrapper {
  width: 100%;
  padding: 0;
  min-height: 265px;
  margin: 0 auto;
  background-size: 320px;
  background-color: $bright-color;
  background-repeat: no-repeat;
  background-image: url("../img/green-cat.png");
  padding-top: 10px;
  text-align: center;
}

.slogan__image {
  display: block;
  margin: auto;
  margin-top: -110px;
}

@media (min-width: $tablet-width) {
  .slogan {
  width: 100%;
  }
  .slogan__wrapper {
  width: 768px;
  margin: 0 auto;
  background-color: inherit;
  background-image: none;
  text-align: left;
  box-sizing: border-box;
  padding-left: 50px;
  }

  .slogan__image {
    width: 60%;
    height: 60%;
  }

}
