.options {
  width: 320px;
  margin: 0 auto;
}

.options__wrapper{
  margin: 0 auto;
}

.options__link {
  display: block;
  width: 280px;
  height: 40px;
  margin: 0 auto;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
}

.options__list {
  margin: 0 auto;
  padding: 0;
}

.options__item {
  list-style: none;
  box-sizing: border-box;
  display: block;
  min-height: 265px;
  width: 280px;
  background-color: $main-color;
  margin: 20px auto;
  padding: 10px 20px;
}

@media (min-width: $tablet-width) {
  .options {
  width: 100%;
  }
  .options__wrapper {
  width: 768px;
  margin: 0 auto;
  }

  .options__item {
  width: 708px;
  }

  .options__link {
  margin-left: 50px;
  margin-top: -500px;
  margin-bottom: 440px;
  }
}
